@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:300,300italic,600,600italic);
.navbar{
    height: auto;
    position: relative;
    z-index: 999;
    box-shadow: 0 6px 6px gray;
    padding: 0;
margin: 0;
}
.navbar-toggler {
    height: inherit;
    
}
.navbar-collapse{
text-align: center;    

}
.nav-link{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 1.5em;
    font-weight: bold;
    height: 100%;
}

#footer {
    position: relative;
    bottom: 0;
    width: 100%;
    height: 2.5rem;      
  }
 .copyright {
        font-size: 12px;
        list-style: none;
        padding-left: 0;
        text-align: center;        
    }


.card-image {
    padding-bottom: 25%;
}
.btn {
    width: 100%;
}
.card-content p b{
    color: black;
    text-align: center;
    font-size: 1.5em;
}



.collection{
    display: flex;
    flex-direction: column;

}
.checkout{
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}
#banner{
    height: 90vh;
}
.content{
    display: flex;
    flex-direction: column;
}

.box{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap
}
.material-icons{
    font-size: 2.4em;
    cursor: pointer;
}
.card{
    width: 300px;
}
.card .card-image .card-title{
    color: black;
    font-weight: 400
}
.cart{
    margin-top:40px;
}
.collection .collection-item{
    display: flex;
}
.collection-item.avatar .title {
    font-size: 20px;
    font-weight: 500;
    color: #ee6e73;
    display: block;
    margin-bottom: 20px;
    
}
.collection .collection-item img{
    width: 150px;
    margin-right: 25px;
}
.item-desc{
    align-self:center;
}

